.app-wrapper {
    display: grid;
    grid-template-rows: 60px 1fr;
    grid-template-columns: 2fr 10fr;
    grid-gap: 10px;
    grid-template-areas:
  'header header'
  'navbar content';
    max-width: 1200px;
    margin: 0 auto;
}

.app-wrapper__content {
    grid-area: content;
    background-color: wheat;
}

